.header {
  padding-top: 10px;
  padding-bottom: 18px;
  background-color: $secondary-white;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &--another {
    &::before {
      display: none;
    }
  }

  @media (min-width: $table-width) {
    &::before {
      background-image: none;
    }
  }


  @media (min-width: $desktop-width) {
    padding-top: 53px;
    position: relative;
    background-color: transparent;

    &::before {
      width: 50%;
      min-height: 700px;
      top: 0;
      right: 0;
      background-image: url("../img/index/intro-decstop-bg.png");
    }
  }
}

.header__container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;

  @media (max-width: $mobile-width-only) {
    padding: 0;
  }

  @media (min-width: $table-width) {
    justify-content: space-between;
    align-items: center;
  }
}

.header__logo {
  display: flex;
  align-items: center;
  flex-grow: 1;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media (max-width: $mobile-width-only) {
    margin-left: 20px;
  }
}

.header__navigation {
  margin-left: auto;
  background-color: $secondary-white;
  flex-wrap: wrap;

  @media (max-width: $mobile-width-only) {
    &--opened {
      display: flex;
      min-height: 198px;
      top: 56px;
      left: 0;
      width: 100%;
      position: absolute;
    }

    &--closed {
      display: none;
    }

    &--nojs {
      margin-top: 18px;
      position: static;
      display: block;
      min-height: 198px;
      width: 100%;
    }
  }

  @media (min-width: $table-width) {
    width: 440px;
  }

  @media (min-width: $desktop-width) {
    width: 482px;
    background-color: transparent;
  }
}

.header__navigation-toggle {
  padding: 0;
  position: relative;
  width: 24px;
  height: 41px;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 13px;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: $secondary-color;
    box-shadow: 0 8px 0 0 $secondary-color, 0 16px 0 0 $secondary-color;
  }

  &--open {
    &::before {
      transform: rotate(45deg);
      top: 18px;
      box-shadow: none;
    }

    &::after {
      content: "";
      position: absolute;
      top: 18px;
      left: 0;
      height: 2px;
      width: 100%;
      transform: rotate(-45deg);
      background-color: $secondary-color;
    }
  }

  @media (max-width: $mobile-width-only) {
    margin-right: 20px;
    margin-left: 20px;
  }

  @media (min-width: $table-width) {
    display: none;
  }
}

.header__navigation-list {
  @include resseting-lists;
  display: block;

  @media (max-width: $mobile-width-only) {
    width: 100%;
  }

  @media (min-width: $table-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.header__navigation-item {
  text-align: center;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $rare-second-gray;
  }

  @media (min-width: $table-width) {
    margin-right: 24px;

    &::before {
      display: none;
    }

    &:nth-child(3n) {
      margin-right: 0;
    }
  }
}

.header__navigation-link {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  color: $secondary-color;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;

  @media (max-width: $mobile-width-only) {
    padding-top: 22px;
    padding-bottom: 24px;
    display: block;
  }

  @media (min-width: $table-width) {
    position: relative;
    font-size: 18px;

    &--active {
      &::before {
        position: absolute;
        content: "";
        left: 0;
        bottom: -10px;
        width: 100%;
        height: 2px;
        background-color: $second-element-bg;
      }
    }
  }

  @media (min-width: $desktop-width) {
    font-size: 20px;
    line-height: 30px;
    color: $secondary-white;
    font-weight: 400;

    &--active {
      &::before {
        background-color: $secondary-white;
      }
    }

    &--another {
      color: $secondary-color;

      &::before {
        background-color: $second-element-bg;
      }
    }
  }
}
